import axios from "axios"
//cookie
import { getCookie, removeCookie } from "./auth"
/**message */
import { MessageBox } from "element-ui"

const instance = axios.create({
  // 
  baseURL: "/",
  // 
  timeout: 36 * 1000,
  /** */
  withCredentials: true,
  // 
  headers: {
    "Content-Type": "application/json; charset=utf-8"
  }
})

// 
instance.interceptors.request.use(
  function (config) {
    // 
    config.headers.Authorization = "bearer" + " " + getCookie()
    return config
  },
  function (error) {
    // 
    return Promise.reject(error)
  }
)

// 
instance.interceptors.response.use(
  function (response) {
    // 2xx 
    // 

    //401
    if (response.data.code === 10002 && response.status == 200) {
      //token
      removeCookie()
      //
      window.location.href = "/login"
    }
    return response.data
  },
  function (error) {
    //  2xx 
    // 
    let tilte = ""
    let message = error.message
    console.log(error, "71")
    // ,code,401,200,302
    // ," 
    // :  error.code
    // : error.message
    // 
    if (error.code) {
      switch (error.code) {
        case 401:
          tilte = ""
          break
        case 400:
          tilte = ""
          break
        case 403:
          tilte = ""
          break
        case 404:
          tilte = ""
          break
        case 405:
          tilte = ""
          break
        case 408:
          tilte = ""
          break
        case 500:
          tilte = ""
          break
        case 501:
          tilte = ""
          break
        case 502:
          tilte = ""
          break
        case 503:
          tilte = ""
          break
        case 504:
          tilte = ""
          break
        case 505:
          tilte = "HTTP"
          break
        default:
          tilte = error.response.status
          return MessageBox.alert(message, tilte, {
            type: "warning"
          })
      }
    } else {
      return MessageBox.alert(", !", "", {
        type: "error"
      })
    }
    return Promise.reject(error)
  }
)

export default instance
